@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@400;500&display=swap);
*{margin:0;padding:0;box-sizing:border-box}.App{padding:30px;font-family:"Mulish", sans-serif;font-size:20px}.App__title{font-size:50px;color:#5e4890;text-align:center;padding:20px 0;text-shadow:0 4px 3px rgba(0,0,0,0.4),0 8px 13px rgba(0,0,0,0.1),0 18px 23px rgba(0,0,0,0.1)}.App__info{text-align:center}

.user{font-size:18px;font-weight:bold;color:gray;transition:all ease-in-out 0.4s;cursor:pointer;text-align:right}.user:hover{color:#f97751}@media (max-width: 900px){.user{text-align:center}}

.todo__info{display:grid;grid-template-columns:3fr 1fr 1fr;grid-gap:10px;align-items:center}@media (max-width: 900px){.todo__info{display:block;text-align:center}}.todo__title{font-weight:bold;color:gray;transition:all ease-in-out 0.4s;cursor:pointer}.todo__title:hover{color:#5e4890}.todo__status{padding:10px}.status__todo{padding:10px;margin:0 auto;font-size:0.7em;letter-spacing:1px;text-align:center;height:40px;border-radius:15px;width:160px;transition:all 0.3s ease-in-out;cursor:pointer;color:#fff}.not{background-color:#f97751;box-shadow:4px 5px 15px #f97751}.not:hover{box-shadow:5px 10px 10px -5px #f97751}.done{background-color:#5e4890;box-shadow:4px 5px 15px #5e4890}.done:hover{box-shadow:5px 10px 10px -5px #5e4890}

.todo__list{list-style:none;box-shadow:4px 5px 15px #5e4890;border-radius:15px;padding:25px 60px;margin-top:15px}@media (max-width: 900px){.todo__item{padding:14px;border-bottom:3px solid indianred}}

